<template>
    <div>      
        <v-skeleton-loader type="card-heading, list-item-three-line" class="mx-auto" :width="500" :height="400" v-if="isLoading"></v-skeleton-loader>
        <table class="table" v-else>
            <thead class="table__head">
                <th class="table__head__data">S.No</th>
                <th class="table__head__data">Name</th>
                <th class="table__head__data">see user data</th>
            </thead>
            <tbody class="body">
                <tr v-for="user in userData" :key="user.id" class="body__row">
                    <td class="body__row__data">{{ user.id }}</td>
                    <td class="body__row__data">{{ user.name }}</td>
                    <td class="body__row__data">
                        <button class="body__row__data__button" @click="handleClick(user)">Check Details</button>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script setup lang="ts">
import { ref, onMounted } from 'vue'
import axios from 'axios'

import router from '../router/index'

interface UserData {
    id: number,
    name: string,
    username: string,
    email: string,
    address: {
        street: string,
        suite: string,
        city: string,
        zipcode: string,
        geo: {
            lat: string,
            lng: string,
        },
    },
    website: string,
    phone: string,
    company: {
        name: string,
        catchPhrase: string,
        bs: string,
    },
}

const userData = ref<UserData[]>();
const isLoading = ref(true);

const handleClick = (user: UserData) => {
    router.replace({ name: 'userDetails', params: { userData: user } });
}

onMounted(async () => {   
    const response = await axios({
        method: 'get',
        url: 'https://jsonplaceholder.typicode.com/users'
    })   
    userData.value = response.data;   
    const loadingTime = setTimeout(() => {
        isLoading.value = false;
        clearTimeout(loadingTime);
    }, 300)
})
</script>

