.table {
    margin: 20px 40px;

    &__head {
        background-color: rgb(207, 108, 91);
        color: white;
        font-size: 18px;
        
        &__data {
            padding: 10px;
        }
    }


    .body__row {
        background-color: rgb(241, 195, 109);

        &:nth-child(odd) {
            background-color: beige;
        }


        &__data {
            padding: 10px;

            &__button {
                padding: 5px 10px;
                border: none;
                color: rgb(102, 69, 8);
                background-color: wheat;
                border-radius: 10px;
                font-weight: 500;
                cursor: pointer;

                &:active {
                    transform: scale(0.98);
                }

                &:hover {
                    background-color: rgb(143, 206, 206);
                }
            }
        }
    }
}
</style>